.restaurant-body {
  flex-grow: 1;
  width: 100%;
  overflow: scroll;
}
    
.restaurant-box {
  height: 30vh;
  width: 100%;
  position:relative;
  border: 1px solid white;
}
  
.restaurant-image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}
  
.top-bar {
  background-color: #c4c4c4ad;
  height: 20%;
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 0px 4vw;
}
  
.bottom-bar {
  background-color: #c4c4c4ad;
  height: 20%;
  position: absolute;
  top: 80%;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 1vw 4vw;
}
  
.address-and-opening {
  height: 100%;
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.rating {
  position: absolute;
  right: 4vw;
}

.restaurant-title{
  font-size: 2.5vh;
}

.restaurant-details-text {
  font-size: 1.6vh;
}