.filter-screen {
    display: contents;
    width: 100px;
    position: relative;
    flex-grow: 1;
    width: 100%;
    overflow: scroll;
}

.top-area {
    display: flex;
    justify-content: space-between;
    white-space: nowrap;
    width: 100%;
    height: 5vh;
    background-color: white;
    border-bottom: 1px solid #B6B6B6;
    position: absolute;
    top: 0vh;
}

.back-block {
    margin: 10px;
}

.back-button{
    font-size: 2.5vh;
  }

.location-area {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    white-space: nowrap;
    width: 100%;
    height: 15vh;
    background-color: white;
    border-bottom: 1px solid #B6B6B6;
    position: absolute;
    top: 5vh;
    padding: 22px 15px;
}

.filter-titles {
    color: gray;
    font-size: 2vh;
}

.filter-text {
    color: black;
    font-size: 2vh;
}



.searchBarFilter {
    background-color: #E4E4E4;
    border-radius: 2vh;
    display: flex;
    align-items: center;
    height: 4vh;
    width: 80vw;
    margin: 0px;
    padding-left: 5px;
    /* margin: 1.5vh 2rem 1rem 3rem; */
}

.radius {
    width: 35vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.km-mi {
    display: flex;
    justify-content: center;
    align-items: center;
}

.button-small{
    background-color: #E4E4E4;
    border-radius: 1vw;
    height: 3vh;
    width: 4vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button-km{
    background-color: #E4E4E4;
    border-top-left-radius: 1vw;
    border-bottom-left-radius: 1vw;
    height: 3vh;
    width: 4vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 1px solid white;
}

.button-mi{
    background-color: #E4E4E4;
    border-top-right-radius: 1vw;
    border-bottom-right-radius: 1vw;
    height: 3vh;
    width: 4vh;
    display: flex;
    justify-content: center;
    align-items: center;
    border-right: 1px solid white;
}

.button-medium{
